import React from 'react'

function Count() {
    return (
        <>

        <section id="counts" className="counts">
        <div className="container" data-aos="fade-up">
          <div className="row g-4">
            <div className="col-6 col-lg-3">
              <div className="count-box">
                <i className="bi bi-emoji-smile"></i>
                <div>
                  <span data-purecounter-start="0" data-purecounter-end="121" data-purecounter-duration="5" className="purecounter"></span>
                  <p>Happy Clients</p>
                </div>
              </div>
            </div>
            <div className="col-6 col-lg-3">
              <div className="count-box">
                <i className="bi bi-people" style={{color: "#bb0852"}}></i>
                <div>
                  <span data-purecounter-start="0" data-purecounter-end="07" data-purecounter-duration="10" className="purecounter"></span>
                  <p>Team Member</p>
                </div>
              </div>
            </div>
            <div className="col-6 col-lg-3">
              <div className="count-box">
                <i className="bi bi-journal-richtext" style={{color: "##ee6c20"}}></i>
                <div>
                  <span data-purecounter-start="0" data-purecounter-end="51" data-purecounter-duration="20" className="purecounter"></span>
                  <p>Projects</p>
                </div>
              </div>
            </div>
            <div className="col-6 col-lg-3">
              <div className="count-box">
                <i className="bi bi-headset" style={{color: "#15be56"}}></i>
                <div>
                  <span data-purecounter-start="0" data-purecounter-end="221" data-purecounter-duration="1" className="purecounter"></span>
                  <p>Support</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
            
        </>
    )
}

export default Count
