import React from 'react';
import {
  BrowserRouter as Router, Switch, Route,} from "react-router-dom";
import Navbar from './component/Navbar';
import Hero from './component/Hero';
import Offer from './component/Offer';
import Footer from './component/Footer';
import Pricing from './component/Pricing';
import Whyus from './component/Whyus';
import Count from './component/Count';
import Services from './component/Services';
import Portfolio from './component/Portfolio';
import Modal from './component/Modal';
import Contfrom from './component/Contfrom';
import Innerpage from './component/Innerpage';



function App() {
  return (
    <Router>
    <Navbar />
      <Switch>
        <Route path="/about">
        <Innerpage name="About Us.." />
        <Whyus />
        <Count />
        </Route>

        <Route path="/services">
        <Innerpage name="Services.." />
          <Services />
          <Count />
          <Pricing />
          </Route>
          
          <Route path="/portfolio">
          <Innerpage name="Portfolio.." />
          <Portfolio />
          </Route>
          
          <Route path="/pricing">
          <Innerpage name="Our Different Package.." />
          <Offer />
          <Pricing />
        </Route>

        <Route path="/Contact">
          <Innerpage name="Tell Us Your Needs.." />
          <Contfrom />
        </Route>

        <Route path="/">
          <Hero />
          <Offer />
          <Whyus />
          <Services />
          <Count />
          <Pricing />
          <Contfrom />
        </Route>

      </Switch>
    <Modal />
    <Footer />
    </Router>
  );
}

export default App;
