import React, { useState } from "react";
import {NavLink } from 'react-router-dom';

function Navbar() {
    const [showMediaIcons, setShowMediaIcons] = useState(false);
    return (
        <>
        <header id="header" className="header fixed-top">
            <div className="container-fluid container-xl d-flex align-items-center justify-content-between">
        
            <NavLink to="/" className="logo d-flex align-items-center">
                <img src="assets/img/logo.png" alt="" />
                <span>Dev..</span>
            </NavLink>


        
            <nav id="navbar" className={showMediaIcons ? "navbar navbar-mobile" : "navbar"}>
                <ul>
                <i className="bi-x" onClick={() => setShowMediaIcons(!showMediaIcons)}></i>
                <li><NavLink className="nav-link scrollto" exact onClick={() => setShowMediaIcons(false)} to="/">Home</NavLink></li>
                <li><NavLink className="nav-link scrollto" onClick={() => setShowMediaIcons(false)} to="/About">About</NavLink></li>
                <li><NavLink className="nav-link scrollto" onClick={() => setShowMediaIcons(false)} to="/Services">Services</NavLink></li>
                <li><NavLink className="nav-link scrollto" onClick={() => setShowMediaIcons(false)} to="/portfolio">Portfolio</NavLink></li>
                <li><NavLink className="nav-link scrollto" onClick={() => setShowMediaIcons(false)} to="pricing">Pricing</NavLink></li>
                <li><NavLink className="nav-link scrollto" onClick={() => setShowMediaIcons(false)} to="/contact">Contact</NavLink></li>
                <li><a className="getstarted scrollto" onClick={() => setShowMediaIcons(false)} href="tel:+917003916407"> Call Now-7003916407</a></li>
                </ul>
                <div className="callnow"><a href="tel:+917003916407"> Call Now</a></div>
                <i className="bi-list" onClick={() => setShowMediaIcons(!showMediaIcons)}></i>
            </nav>
            </div>
        </header>        
        </>
    )
}

export default Navbar
