import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { db } from "./firebase";

function Contfrom() {
  const { pathname } = useLocation();
  useEffect(() =>{
      window.scrollTo(0, 0);
  }, []);
        const [name, setName] = useState("");
        const [email, setEmail] = useState("");
        const [subject, setSubject] = useState("");
        const [message, setMessage] = useState("");
      
        const [loader, setLoader] = useState(false);
      
        const handleSubmit = (e) => {
          e.preventDefault();
          setLoader(true);
      
          db.collection("contacts")
            .add({
              Name: name,
              Email: email,
              Subject: subject,
              Message: message,
            })
            .then(() => {
              setLoader(false);
              alert("Your message has been submitted");
            })
            .catch((error) => {
              alert(error.message);
              setLoader(false);
            });
      
          setName("");
          setEmail("");
          setSubject("");
          setMessage("");
        };

    return (
        <>
        <section id="contact" className="contact">

        <div className="container" data-aos="fade-up">
  
          <header className="section-header">
            <h2>Contact</h2>
            <p>Contact Us</p>
          </header>
  
          <div className="row gy-4">
            <div className="col-lg-6">
              <div className="row gy-4">
                <div className="col-md-12">
                  <div className="info-box d-flex">
                    <i className="bi bi-telephone mt-4 p-1 fw-bold"></i>
                    <div className="ps-4">
                    <h3>Call Us</h3>
                    <p> <a className="fw-bold" href="tel:+917003916407"> Call Now- 7003916407</a></p>
                    </div>
                  </div>
                </div>
                <div className="col-md-12">
                  <div className="info-box d-flex">
                    <i className="bi bi-envelope mt-4 p-1"></i>
                    <div className="ps-4">
                    <h3>Email Us</h3>
                    <p> <a className="fw-bold" href="mailto:"> info@example.com</a> <br />
                    <a className="fw-bold" href="mailto:">contact@example.com</a></p>
                   </div>
                  </div>
                </div>
              </div>
            </div>
  
            <div className="col-lg-6">
              <form onSubmit={handleSubmit} id="Contact-from" className="react-email-form">
                <div className="row gy-4">
  
                  <div className="col-md-6">
                    <input type="text" name="name" id="Name1" className="form-control" placeholder="Your Name" required  
                    value={name}
                    onChange={(e) => setName(e.target.value)} />
                  </div>
  
                  <div className="col-md-6 ">
                    <input type="email" className="form-control" name="Sender" id="Sender1" placeholder="Your Email" required 
                    value={email}
                    onChange={(e) => setEmail(e.target.value)} />
                  </div>
  
                  <div className="col-md-12">
                    <input type="text" className="form-control" name="subject" id="Subject" placeholder="Subject" 
                    value={subject}
                    onChange={(e) => setSubject(e.target.value)} />
                  </div>
  
                  <div className="col-md-12">
                    <textarea className="form-control" name="message" id="Message1" rows="6" placeholder="Message" 
                    value={message}
                    onChange={(e) => setMessage(e.target.value)} ></textarea>
                  </div>
  
                  <div className="col-md-12 text-center">
                    <button type="submit" className="btn_submit" style={{ background: loader ? "#ccc" : " rgb(2, 2, 110)" }}>Send Message</button>
                  </div>
                </div>
              </form>
            </div>
  
          </div>
        </div>
      </section>

            
        </>
    )
}

export default Contfrom
