import React, { useState, useEffect } from "react";
import { db } from "./firebase";

function Modal() {


  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [budget, setBudget] = useState("");
  const [message, setMessage] = useState("");

  const [loader, setLoader] = useState(false);


  const handleSubmit = (e) => {
    e.preventDefault();
    setLoader(true);

    db.collection("contacts")
      .add({
        Name: name,
        Email: email,
        Phone: phone,
        Budget: budget,
        Message: message,
      })
      .then(() => {
        setLoader(false);
        alert("Your message has been submitted");
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setName("");
    setEmail("");
    setPhone("");
    setBudget("");
    setMessage("");
  };


    return (
        <>
      <div className="modal fade contact" id="exampleModal" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="exampleModalLabel">Submit Your Necessity</h5>
            <button id="test111" type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div className="modal-body">
              <form  className="react-email-form" onSubmit={handleSubmit} id="modal-from">

                <div className="row gy-4">
                  <div className="col-md-6">
                    <input type="text" name="name"  className="form-control" id="Name" placeholder="Your Name" required 
                    value={name}
                    onChange={(e) => setName(e.target.value)} />
                  </div>
  
                  <div className="col-md-6 ">
                    <input type="email" className="form-control" name="email" id="Sender" placeholder="Your Email" required 
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}/>
                  </div>
  
                  <div className="col-md-6">
                    <input type="phone" className="form-control" name="phone" id="phone" placeholder="Phone no." required 
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}/>
                  </div>

                  <div className="col-md-6">
                    <label htmlFor="Budget">Select Option:</label>
                      <select id="Budget" 
                      value={budget}
                      onChange={(e) => setBudget(e.target.value)}>
                        <option value="">Select</option>
                        <option value="Business Website">Business Website</option>
                        <option value="E-Commerce Website">E-Commerce Website</option>
                        <option value="ocial Media Post">Social Media Post</option>
                        <option value="Profesional Logo">Profesional Logo</option>
                        <option value="Other">Other</option>
                      </select>
                  </div>
  
                  <div className="col-md-12">
                    <textarea className="form-control" name="message" id="Message" rows="6" placeholder="Message"
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    ></textarea>
                  </div>
  
                  <div className="col-md-12 text-center">
                  <button type="submit" className="btn-get-started" style={{ background: loader ? "#ccc" : " rgb(2, 2, 110)" }}>Send Message</button>
                  </div>
  
                </div>
              </form>
          </div>
        </div>
      </div>
    </div>
            
        </>
    )
}

export default Modal
